<template>
  <div class="container">
    <div class="table-filter">
      <div class="row">
        <div class="col-sm-3"></div>
        <!-- <div class="col-sm-3">
              <div class="show-entries">
                <span>Show</span>
                <select class="form-control">
                  <option>5</option>
                  <option>10</option>
                  <option>15</option>
                  <option>20</option>
                </select>
                <span>entries</span>
              </div>
        </div>-->
        <div class="col-sm-9">
          <div class="float-right">
            <div class="btn-group">
              <a class="filter-icon d-inline-block pr-2 text-dark" data-toggle="dropdown" href="#">
                <i class="fa fa-filter"></i>
              </a>
              <ul class="dropdown-menu">
                <li class="dropdown-item" @click="trackBy='user'">User</li>
                <li class="dropdown-item" @click="trackBy='orderId'">Order Id</li>
              </ul>
            </div>
            <div class="filter-group d-inline-block" v-if="trackBy=='user'">
              <label>Name</label>
              <input type="text" v-model="trackData[1]" class="form-control" />
            </div>
            <div class="filter-group d-inline-block">
              <label v-if="trackBy=='user'">Email</label>
              <label v-if="trackBy=='orderId'">Order Id</label>
              <input type="text" v-model="trackData[0]" class="form-control" />
            </div>
            <a class="btn btn-primary text-dark" @click="getData()">Track</a>
          </div>
        </div>
      </div>
    </div>

    <!--  order orders -->
    <div class="row p-4 m-4" v-if="order">
      <div class="col-12">
        <div class="card content border-1 shadow-sm mt-4">
          <div class="card-body">
            <OrderStatus :statuses="order.track_status"></OrderStatus>

            <div class="row">
              <div class="col-12">
                <!-- order -->
                <div class="card content border-1 shadow-sm mb-4">
                  <div class="card-body container-fluid">
                    <div class="row order-content">
                      <div class="col-4 order-by-header">
                        <!-- order guy -->
                        <div class="media pt-2">
                          <img
                            src="https://vignette.wikia.nocookie.net/joshua-eatons-stupid-jokes/images/9/97/C80902787040a7562566bd10cb4a44e0--college-hairstyles-hairstyles-for-guys.jpg/revision/latest?cb=20170809214246"
                            class="img-fluid rounded-circle align-self-center"
                          />
                          <div class="media-body pl-4">
                            <h6>Mr. Ahmad</h6>
                          </div>
                        </div>
                      </div>
                      <div class="col-8" style="font-size:small;">
                        <div>
                          <span class="font-weight-bold">Order Address</span>
                          <span class="response">{{order.deliveryAddress}}</span>
                        </div>
                        <div>
                          <span class="font-weight-bold">Ordered At</span>
                          <span class="response">{{order.orderDate}}, {{order.orderTime}}</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <!-- -->
                <div class="card content border-1 shadow-sm">
                  <div class="card-body">
                    <div class="table-responsive pt-4">
                      <table
                        class="table table-hover table-sm table-borderless table-content text-left"
                      >
                        <thead>
                          <tr>
                            <th>Product Id</th>
                            <th>Products</th>
                            <th>Product Name</th>
                            <th>Cost Per Item</th>
                            <th>Ordered Quantity</th>
                            <th>Total Cost</th>
                            <th>Unit</th>
                            <th>Availability Status</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="product in order.products" :key="product.id">
                            <td>{{product.productId}}</td>
                            <td>
                              <div class="d-flex">
                                <img :src="product.image" class="img-fluid content-image" />
                              </div>
                            </td>
                            <td>{{product.name}}</td>
                            <td>{{product.currentPrice}}</td>
                            <td>{{product.quantity}}</td>
                            <td>{{product.quantity * product.currentPrice}}</td>
                            <td class="text-dark">{{product.qty}}{{product.unit}} x 20</td>
                            <td class="text-dark">
                              <span
                                class="bagde badge-paid pl-1 pr-1"
                                v-if="product.quantity<=product.stock"
                              >Available</span>
                              <span class="bagde badge-failed pl-1 pr-1" v-else>Not Available</span>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!--  user orders -->
    <div class="row" v-if="trackedUser != null || userOrders.length>0">
      <div class="col-12">
        <div class="card content border-1 shadow-sm mt-4">
          <div class="card-body">
            <div class="table-responsive pt-2">
              <table class="table table-hover table-sm table-borderless table-content text-left">
                <thead>
                  <tr>
                    <th>S.No.</th>
                    <th>OrderId</th>
                    <th>Description</th>
                    <th>Amount</th>
                    <th>Payment Method</th>
                    <th>Status</th>
                    <th>Placed On</th>
                    <th>View</th>
                  </tr>
                </thead>
                <tbody v-if="userOrders.length>0">
                  <tr v-for="(order,index) in userOrders" :key="index+1">
                    <td>{{index+1}}</td>
                    <td>{{order.orderId}}</td>
                    <td>
                      <div v-if="order.products.length>2">
                        <span
                          v-for="product in order.products.slice(0,2)"
                          :key="product.productId"
                        >{{product.name}} +</span>
                        <span>
                          <b>{{order.products.length-2}} more</b>
                        </span>
                      </div>
                      <div v-else>
                        <span
                          v-for="product in order.products"
                          :key="product.productId"
                        >{{product.name}}</span>
                      </div>
                    </td>
                    <td>{{order.totalPayableAmount}}</td>
                    <td>{{order.paymentMode}}</td>
                    <td>{{order.status}}</td>
                    <td>{{order.orderDate}} at {{order.orderTime}}</td>
                    <td>
                      <button
                        class="btn btn-sm btn-go-green font-weight-bold d-inline"
                        type="submit"
                        @click="getOrder(index)"
                      >View</button>
                    </td>
                  </tr>
                </tbody>

                <tbody v-else>
                  <tr>
                    <td colspan="8" class="text-center">
                      <div>
                        <img
                          class="card-img-top img-fluid"
                          src="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/c23778b9-5990-4216-a73c-caa91ab40615/d74k79-e85ac748-18aa-499e-8ea7-159209e3714a.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..guiz9Ehkmrl3p_6osxJfyGNSrOnZxS6TcfaRgS8bIzQ"
                          alt="Card image cap"
                          style="width:20em"
                        />
                        <div class="card-body">
                          <p class="card-text">Oops! There ain't any Orders Made</p>
                        </div>
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>

    <Loader :disable="loading" :success="success" :message="message" />
  </div>
</template>

<script>
import axios from "axios";
import Vue from "vue";
import { domain } from "@/store/variable";
import store from "@/store/store";
import Loader from "../../Loader";
import OrderStatus from "./orderStatus.vue";

export default {
  name: "TrackOrder",
  components: {
    Loader,
    OrderStatus
  },
  data() {
    return {
      trackBy: "user",
      trackData: [],
      order: null,
      trackedUser: null,
      userOrders: [],

      loading: false,
      success: false,
      message: "Searching"
    };
  },

  computed: {
    user() {
      return store.state.user;
    }
  },

  methods: {
    getData() {
      if (this.user && this.user.designation == "admin") {
        let url = domain + "order";
        this.order = null;

        this.trackData.forEach(doc => {
          doc = doc.trim();
        });

        if (this.trackBy == "orderId") {
          this.loading = true;
          axios
            .get(url, {
              params: {
                id: this.trackData[0]
              },
              headers: {
                from: "android"
              }
            })
            .then(res => {
              res.data.forEach(doc => {
                this.order = doc;
              });
              this.loading = false;
            });
        } else {
          let userURL = domain + "user";
          this.loading = true;
          axios
            .get(userURL, {
              params: {
                email: this.trackData[0]
              }
            })
            .then(res => {
              res.data.forEach(doc => {
                this.trackedUser = doc;
              });

              if (this.trackedUser) {
                axios
                  .get(url, {
                    params: {
                      userId: this.trackedUser["uuid"]
                    },
                    headers: {
                      from: "android"
                    }
                  })
                  .then(res => {
                    this.userOrders = res.data;
                    this.loading = false;
                  });
              } else {
                this.success = true;
                this.loading = false;
                this.message = "User not found";

                setTimeout(() => {
                  this.success = false;
                  this.message = "Searching";
                }, 2000);
              }
            });
        }
      }
    },

    getOrder(index) {
      if (this.userOrders) {
        this.order = this.userOrders[index];
      }
    }
  }
};
</script>

<style scoped>
.order-by-header h6 {
  color: rgba(47, 48, 61, 1);
  font-weight: 700;
  margin-bottom: 0.2em;
}

.order-by-header .heading {
  color: rgba(47, 48, 61, 0.82);
  font-weight: 600;
}

.order-by-header .location {
  color: rgba(47, 48, 61, 0.82);
  font-weight: 600;
  font-size: smaller;
}

.order-content {
  font-size: small;
}

.order-content img {
  width: 4em;
}

.response {
  color: rgba(47, 48, 61, 0.82);
  font-weight: 700;
}

.table-filter input,
.table-filter select {
  height: 34px;
  border-radius: 3px;
  border-color: #ddd;
  box-shadow: none;
}
.table-filter {
  padding: 5px 0 15px;
  border-bottom: 1px solid #e9e9e9;
}
.table-filter .btn {
  height: 34px;
}
.filter-group select.form-control {
  width: 110px;
}
.filter-icon i {
  font-size: 18px;
  opacity: 0.7;
}
.filter-icon {
  display: inline-block;
}
</style>



