<template>
  <!-- modal -->
  <div class="modal fade" role="dialog" id="deleteItem">
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <slot name="header">
            <h6 class="modal-title">
              <i class="fas fa-exclamation-circle text-danger" style="font-size:21px"></i>
              <div
                class="d-inline modal-header-style pl-2"
                style="font-size:21px; opacity:.95"
              >Alert ! Proceed With Precaution</div>
            </h6>
          </slot>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <slot name="body">
            <div style="opacity:0.85;" class="font-weight-bold">Delete this data from DB ?</div>
            <div class="alert-box-style p-3 mt-3 mb-2">
              <div style="font-weight:600">Name: {{item.name}}</div>
              <div style="font-weight:600">Category: aaegi</div>
              <div style="font-weight:600">SubCategory: aaegi</div>
              <div style="font-weight:600">CoCategory: aaegi</div>
            </div>
          </slot>
          <small style="opacity:0.75 mt-4">Remember you can't undo this</small>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary btn-sm font-weight-bold"
            data-dismiss="modal"
          >
            <i class="fas fa-ban pr-2"></i>CANCEL
          </button>
          <button
            type="button"
            class="btn btn-delete btn-sm btn-danger font-weight-bold"
            data-dismiss="modal"
            @click="$emit('deleteItem(index)')"
          >
            <i class="fas fa-trash pr-2"></i>DELETE
          </button>
        </div>
      </div>
    </div>
  </div>
  <!-- modal end -->
</template>

<script>
export default {
  props: {
    item: Object,
    index: Number
  }
};
</script>
<style>
.alert-box-style {
  background-color: rgb(251, 233, 231);
  color: rgb(255, 82, 82);
  letter-spacing: 0.3px;
}

.modal-header-style {
  font-weight: bolder;
}
</style>





