<template>
  <div>
    <div
      class="text-center mt-1 mb-1"
      style="color:#000000;background-color:#fafafa;font-size:18px;font-weight:700;"
    >PRODUCTS</div>
    <div class="table-responsive-md">
      <!-- adding modal -->
      <!-- <modal
        v-if="showDeleteModal"
        @close="showDeleteModal[index]=false"
        :item="modalData"
        :index="modalIndex"
      ></modal>-->
      <table class="table table-striped table-bordered table-sm table-hover">
        <thead class="thead-dark">
          <tr style="background-color:#EEEEEE">
            <th>DoD</th>
            <th data-toggle="tooltip" data-placement="bottom" title="name">
              Name of items
              <i
                class="float-right fa fa-arrow-down"
                v-if="orderWay=='asc' && orderItems=='name'"
                @click="order('name','desc')"
              ></i>
              <i
                class="float-right fa fa-arrow-up"
                v-else-if="orderWay=='desc' && orderItems=='name'"
                @click="order('name','asc')"
              ></i>
              <i
                class="float-right fas fa-chevron-down"
                v-else
                @click="order('name','asc')"
                @dblclick="order('currentPrice','desc')"
              ></i>
            </th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Main or parent category of items"
            >Category</th>
            <th data-toggle="tooltip" data-placement="bottom" title="subcategory">SubCategory</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Co category is visible in tab activity of app"
            >Co-category</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Stock depicts the number of particular item in inventory"
            >
              Stock
              <i
                class="float-right fa fa-arrow-down"
                v-if="orderWay=='asc' && orderItems=='stock'"
                @click="order('stock','desc')"
              ></i>
              <i
                class="float-right fa fa-arrow-up"
                v-else-if="orderWay=='desc' && orderItems=='stock'"
                @click="order('stock','asc')"
                @dblclick="order('stock','asc')"
              ></i>
              <i class="float-right fas fa-chevron-down" v-else @click="order('stock','asc')"></i>
            </th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Unit(kg, gram, dozens, etc)"
            >Unit</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Printed Price of an item in rupees"
            >
               MRP
              <i
                class="float-right fa fa-arrow-down"
                v-if="orderWay=='asc' && orderItems=='mrp'"
                @click="order('mrp','desc')"
              ></i>
              <i
                class="float-right fa fa-arrow-up"
                v-else-if="orderWay=='desc' && orderItems=='mrp'"
                @click="order('mrp','asc')"
                @dblclick="order('mrp','asc')"
              ></i>
              <i class="float-right fas fa-chevron-down" v-else @click="order('mrp','asc')"></i>
            </th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Current price of items set by us"
            >
              Current Price (  )
              <i
                class="float-right fa fa-arrow-down"
                v-if="orderWay=='asc' && orderItems=='currentPrice'"
                @click="order('currentPrice','desc')"
              ></i>
              <i
                class="float-right fa fa-arrow-up"
                v-else-if="orderWay=='desc' && orderItems=='currentPrice'"
                @click="order('currentPrice','asc')"
              ></i>
              <i
                class="float-right fas fa-chevron-down"
                v-else
                @click="order('currentPrice','asc')"
                @dblclick="order('currentPrice','asc')"
              ></i>
            </th>
            <th data-toggle="tooltip" data-placement="bottom" title="Discount set by us">Discount(%)</th>
            <!-- <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Edit and delete button"
            >Edit/Delete Buttons</th>-->
          </tr>
        </thead>
        <tbody v-if="items.length>0" style="font-weight:300; opacity:75%" class="table-hover">
          <tr v-for="(item, index) in items" :key="index">
            <td>
              <form>
                <input
                  type="checkbox"
                  @click.prevent="addToDeal(index)"
                  v-model="item.disVal"
                  :checked="item.disVal"
                  :disabled="disable"
                />
              </form>
            </td>
            <td class="font-weight-bold" style="font-size:14px; opacity:.85">{{item.name}}</td>
            <td>{{item.catName}}</td>
            <td>{{item.subcatName}}</td>
            <td>{{item.cocatName}}</td>
            <td class="font-weight-bold">{{item.stock}}</td>
            <td class="font-weight-normal">{{item.unit}}</td>
            <td class="font-weight-bold">{{item.mrp}}</td>
            <td class="font-weight-bold">{{item.currentPrice}}</td>
            <td class="font-weight-bold">{{item.discount}}</td>
            <!-- <td>
              <a
                class="fa fa-trash modal-trigger pr-2"
                style="color:#424242;"
                href="#"
                data-toggle="modal"
                data-target="#deleteItem"
                data-placement="bottom"
                title="delete this item"
                @click="deleteItem(index)"
              ></a>

              <router-link :to="{name: 'editItem', params: {productId: item.productId}}">
                <i class="fa fa-pencil-alt pl-2" style="color:#424242;"></i>
              </router-link>
            </td>-->
          </tr>
        </tbody>
        <tbody v-else>
          <tr>
            <td colspan="10" class="border-0">No Item to show!</td>
          </tr>
        </tbody>

        <tbody v-if="loading">
          <tr>
            <td colspan="10" class="loading border-0">
              <div style="font-weight:700;font-size:20px">
                <span class="alert alert-danger text-danger">Loading data please wait...</span>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import Vue from "vue";
import _ from "lodash";
import $ from "jquery";
import db from "../firebaseInit";
import { firestorage } from "../firebaseInit";
import { domain } from "@/store/variable";
import firebase from "firebase";
import modal from "./Modal";
export default {
  name: "dealOfDay",
  props: {
    products: Array,
    loading: Boolean
  },

  components: {
    modal
  },

  data() {
    return {
      //showDeleteModal: [],
      items: [],
      orderItems: "name",
      orderWay: "asc",
      disable: false

      // modalData: null,
      // modalIndex: 0
    };
  },

  created() {
    this.fetchItems();
  },

  mounted() {
    this.loading = "true";

    this.items = _.orderBy(this.items, "name", "asc");

    Vue.nextTick(function() {
      $('[data-toggle="tooltip"]').tooltip();
    });
  },

  methods: {
    addToDeal(index) {
      this.disable = true;
      let url = domain + "item/" + this.items[index].productId;
      let data = 0;
      if (this.items[index].disVal == 0) {
        data = 1;
      } else {
        data = 0;
      }
      axios
        .post(
          url,
          {
            _method: "put",
            data: {
              dis_val: data
            }
          },
          {
            withCredentials: true,
            headers: {
              "Content-Type": "application/json",
              "Access-Control-Allow-Origin": "*",
              "X-Requested-With": "XMLHttpRequest",
              "X-api-token": firebase.auth().currentUser.uid
            }
          }
        )
        .then(res => {
          Vue.set(this.items[index], "disVal", res.data.dis_val);
          this.disable = false;
        })
        .catch(err => {
          this.disable = false;
          console.log(err);
        });

      this.disable = false;
    },
    fetchItems() {
      // loading content
      let url = domain + "item";
      axios
        .get(url)
        .then(res => {
          res.data.forEach(doc => {
            if (doc.discount < 90 && doc.discount > 60) {
              console.log(doc);
              var item = new Array();
              item = {
                productId: doc.productId,
                itemId: doc.itemId,
                name: doc.name,
                cat: doc.allcat,
                stock: doc.stock,
                unit: doc.unit,
                mrp: doc.MRP,
                currentPrice: doc.currentPrice,
                discount: doc.discount,
                image: doc.image,
                disVal: doc.dis_val
              };

              // getting co cat, subcat and cat
              // let Courl = domain + "cocat";
              // axios
              //   .get(Courl, {
              //     params: {
              //       item_id: item.itemId
              //     }
              //   })
              //   .then(crres => {
              //     crres.data.forEach(doc => {
              //       item["cocatName"] = doc.name;
              //       item["cocatId"] = doc.coid;

              //       let Suburl = domain + "subcat";
              //       axios
              //         .get(Suburl, {
              //           params: {
              //             coid: doc.coid
              //           }
              //         })
              //         .then(subres => {
              //           subres.data.forEach(sub => {
              //             item["subcatName"] = sub.name;
              //             item["subcatId"] = sub.subcatid;

              //             let Caturl = domain + "cat";
              //             axios
              //               .get(Caturl, {
              //                 params: {
              //                   subcatcode: sub.subcatid
              //                 }
              //               })
              //               .then(catres => {
              //                 catres.data.forEach(doc => {
              //                   item["catName"] = doc.name;
              //                   console.log(item);
              //                 });
              //               });
              //           });

              //         });
              //     });
              //   });
              this.loading = false;
              this.items.push(item);
            }
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    showModal(item, index) {
      this.modalData =
        " <strong style='opacity:0.75>Are you sure you wanna delete this data ?</strong><div><div>Name: '+item.name+'/div><div>Category: aaegi</div><div>SubCategory: aaegi</div><div>CoCategory: aaegi</div></div>";
      this.modalIndex = index;
      this.showDeleteModal[index] = true;
    },

    order(On, Way) {
      this.orderItems = On;
      this.orderWay = Way;

      if ((this.orderWay = "desc")) {
        this.items = _.orderBy(this.items, this.orderItems, "desc");
      } else {
        this.items = _.orderBy(this.items, this.orderItems, "asc");
      }
    }
  }
};
</script>

<style>
.loading {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.table {
  margin-bottom: 60px;
}

.footer {
  height: 50px;
}
th {
  font-size: 14px;
  opacity: 65%;
  font-weight: 500;
}
</style>





