<script>
import axios from "axios";
import Vue from "vue";
import { domain } from "@/store/variable";
import { utcHours } from "@/store/variable";
import { utcMins } from "@/store/variable";

export default {
  name: "ViewDelivery",

  data() {
    return {
      orderId: null,
      products: [],
      orderId: null,
      deliveryAddress: null,
      paidAmount: 0,
      payableAmount: 0,
      deliveryId: null,
      deliveryBy: null,
      orderDate: null,
      orderTime: null,
      deliveryDate: null,
      deliveryTime: null,
      deliveryStatus: null,

      search: null,
      searchArray: []
    };
  },

  beforeRouteEnter(to, from, next) {
    let url = domain + "delivery";
    axios
      .get(url, {
        params: {
          id: to.params.deliveryId,
          by: "budhbak",
          userAddress: "budhbak",
          inventoryAddress: "budhbak"
        }
      })
      .then(res => {
        res.data.forEach(doc => {
          let orderDateTime = new Date(doc.order.orderTime);
          orderDateTime.setHours(orderDateTime.getHours() + utcHours);
          orderDateTime.setMinutes(orderDateTime.getMinutes() + utcMins);
          let orderDate =
            orderDateTime.getDate() +
            "/" +
            orderDateTime.getMonth() +
            "/" +
            orderDateTime.getFullYear();
          let orderTime =
            orderDateTime.getHours() +
            ":" +
            orderDateTime.getMinutes() +
            ":" +
            orderDateTime.getSeconds();
          let deliveryDate = null;
          let deliveryTime = null;
          if (doc.deliveredAt) {
            let deliveryDateTime = new Date(doc.deliveredAt);
            deliveryDateTime.setHours(deliveryDateTime.getHours() + utcHours);
            deliveryDateTime.setMinutes(
              deliveryDateTime.getMinutes() + utcMins
            );
            deliveryDate =
              deliveryDateTime.getDate() +
              "/" +
              deliveryDateTime.getMonth() +
              "/" +
              deliveryDateTime.getFullYear();
            deliveryTime =
              deliveryDateTime.getHours() +
              ":" +
              deliveryDateTime.getMinutes() +
              ":" +
              deliveryDateTime.getSeconds();
          }
          next(vm => {
            vm.orderId = doc.orderId;
            vm.deliveryId = doc.id;
            vm.deliveryBy = {
              area: doc.by.area,
              name: doc.by.user.name
            };
            vm.deliveryAddress = doc.order.user_address;
            vm.orderDate = orderDate;
            vm.orderTime = orderTime;
            vm.deliveryDate = deliveryDate;
            vm.deliveryTime = deliveryTime;
            vm.paidAmount = doc.order.paidAmount;
            vm.payableAmount = doc.order.totalPayableAmount;
            vm.deliveryStatus = doc.status;
            //products
            doc.order.products.forEach(prod => {
              vm.products.push(prod);
            });
            vm.searchArray = vm.products;
          });
        });
      })
      .catch(err => {
        console.log(err.message);
      });
  },

  watch: {
    $route: "fetchDelivery"
  },

  methods: {
    fetchDelivery() {
      let url = domain + "delivery";
      axios
        .get(url, {
          params: {
            id: this.$route.params.deliveryId,
            by: "budhbak",
            userAddress: "budhbak",
            inventoryAddress: "budhbak"
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            let orderDateTime = new Date(doc.order.orderTime);
            orderDateTime.setHours(orderDateTime.getHours() + utcHours);
            orderDateTime.setMinutes(orderDateTime.getMinutes() + utcMins);
            let orderDate =
              orderDateTime.getDate() +
              "/" +
              orderDateTime.getMonth() +
              "/" +
              orderDateTime.getFullYear();
            let orderTime =
              orderDateTime.getHours() +
              ":" +
              orderDateTime.getMinutes() +
              ":" +
              orderDateTime.getSeconds();
            let deliveryDate = null;
            let deliveryTime = null;
            if (doc.deliveredAt) {
              let deliveryDateTime = new Date(doc.deliveredAt);
              deliveryDateTime.setHours(deliveryDateTime.getHours() + utcHours);
              deliveryDateTime.setMinutes(
                deliveryDateTime.getMinutes() + utcMins
              );
              deliveryDate =
                deliveryDateTime.getDate() +
                "/" +
                deliveryDateTime.getMonth() +
                "/" +
                deliveryDateTime.getFullYear();
              deliveryTime =
                deliveryDateTime.getHours() +
                ":" +
                deliveryDateTime.getMinutes() +
                ":" +
                deliveryDateTime.getSeconds();
            }

            // putting data
            this.orderId = doc.orderId;
            this.deliveryId = doc.id;
            this.deliveryBy = {
              area: doc.by.area,
              name: doc.by.user.name
            };
            this.deliveryAddress = doc.order.user_address;
            this.orderDate = orderDate;
            this.orderTime = orderTime;
            this.deliveryDate = deliveryDate;
            this.deliveryTime = deliveryTime;
            this.paidAmount = doc.order.paidAmount;
            this.payableAmount = doc.order.totalPayableAmount;
            this.deliveryStatus = doc.status;

            //products
            doc.products.forEach(prod => {
              this.products.push(prod);
            });
          });
        })
        .catch(err => {
          console.log(err.message);
        });
    },

    searching() {
      if (this.search) {
        this.products = this.searchArray.filter(
          v => v.name.toLowerCase().indexOf(this.search.toLowerCase()) != -1
        );
      } else {
        this.products = this.searchArray;
      }
    },

    updateAction() {
      if (this.user.designation == "admin") {
        this.loading = true;
            if (this.action == "delete") {
              if (confirm("You are about to delete #" + this.deliveryId)) {
                let url = domain + "delivery/" + this.deliveryId;
                axios
                  .post(
                    url,
                    {
                      _method: "DELETE"
                    },
                    {
                      withCredentials: true,
                      headers: {
                        "Content-Type": "application/json",
                        "Access-Control-Allow-Origin": "*",
                        "X-Requested-With": "XMLHttpRequest",
                        "X-api-token": firebase.auth().currentUser.uid
                      }
                    }
                  )
                  .then(res => {
                    if (res.data) {
                      this.status = res.data.status;
                    }
                  })
                  .catch(err => {
                    console.log(err.message);
                  });
              }
            } else {
              let url = domain + "delivery/" + delivery.deliveryId;
              axios
                .post(
                  url,
                  {
                    _method: "PUT",
                    status: this.action
                  },
                  {
                    withCredentials: true,
                    headers: {
                      "Content-Type": "application/json",
                      "Access-Control-Allow-Origin": "*",
                      "X-Requested-With": "XMLHttpRequest",
                      "X-api-token": firebase.auth().currentUser.uid
                    }
                  }
                )
                .then(res => {
                  //colsing inputs
                  res.data.forEach(snap => {
                    Vue.set(this.deliveries[index], status, snap.status);
                  });
                })
                .catch(err => {
                  console.log(err.message);
                });
            }
        this.loading = false;
      } else {
        confirm("You are not allowed to make changes");
      }
    },
  }
};
</script>

